#Navbar{
  display: flex;
  flex-direction: row;
  position: sticky;
  justify-content: space-between;
  background-color: #d9975a;
  width:100%;
  align-items: center;
  height:15.5vh;
  top:0px;
  z-index: 10;

  h1{
    display: flex;
    align-items: center;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  }
  #vinyl{
    padding-left:10px;
    padding-right:15px;
  }
  ul{
    list-style-type: none;
    padding: 0;
    margin: 0;
    padding-top: 5px;
  }
  li {
    float: left;
    padding-right: 20px;
    font-size: large;
  }
  a, a:visited, a:hover, a:active {
    color:inherit;
  }
  a{
    display: block;
    padding: 8px;
    text-decoration: none;
  }
  #shopping-cart{
    padding-left: 10px;
    padding-right: 25px;
  }
  #shopping-cart-a{
    padding-top:5px;
    border-bottom:0px;
    position: relative;
  }
  #cartNumber{
    background-color: red;
    color:white;
    border-radius: 50%;
    width:20px;
    height:20px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    bottom:0;
    right:0;
    transform: translate(-110%,-30%);



    p{
      text-align: center;
      font-size:small;
    }
  }
}